
h2 {
  font-size: rem(32px);
  margin-bottom: rem(20px);
  line-height: 1.33;
}
h3{
  font-size: rem(22px);
  @include rem2(margin, 20px 0);
  @media screen and (min-width: 768px) {
    font-size: rem(18px);
  }
}
h4{
  font-size: rem(13px);
}
h1, h2, h3, h4{
  font-weight: 600;
//  font-family: $font-primary-semibold;
  text-transform: uppercase;
  color: $black;
  -webkit-text-stroke: 0.3px;
}

.page-title{
  @include rem2(margin, 20px 0);
  @media screen and (min-width: 1024px) {
    margin-top:0;
  }
}
p{
  font-family: "Gotham-Book";
  font-size: rem(14px);
}