.content_page .c-index.c-index_library{
    @media only screen and (min-width: 768px) and (max-width: 1024px) {
        grid-template-columns: repeat(2, calc(50% - 8px));
        header{
            grid-column-end: 3; 
        }
    }
    .o-index_item img {
        margin: auto;
        box-shadow: 0px 3px 20px 0px #adabab;
    }
    .o-index_price {
        position: absolute;
        top: 20px;
        left: 45px;
        background: #000;
        color: #fff;
        padding: 15px;
        display: block;
        width: 125px;
        font-family: "Gotham-Book";
    }
    .o-index_content {
        margin-top: 20px;
    }
}
