.c-index{
  display: grid;
  grid-template-columns: repeat(3, 390px);
  grid-gap: 30px;
  position: relative;
  z-index: 1;
  @media only screen and (max-width: 767px) {
    flex-wrap: wrap;
    display: flex;
    flex-direction: column;
  }
  h2 {
    margin: 50px 0;
    width: auto;
  }
  p{
    font-size: rem(14px);
    // @media only screen and (max-width: 768px) {
    //     font-size: rem(12px);
    // }
  }
  a {
    @media only screen and (max-width: 767px) {
      margin-bottom: 40px;
    }
  }
  header {
    width: 100%;
    flex-shrink: 0;
    grid-column-start: 1;
    grid-column-end: 4;
  }
  .o-info-box { // TODO: PRZENIESC DO IFOBOX
    position: absolute;
    top: 20px;
    left: 20px;
    width: calc(100% - 70px);
    @media only screen and (max-width: 767px) {
      top: 15px;
      left: 15px;
      padding: 5px;
      font-size: rem(12px);
      &:before {
          margin-right: 5px;
      }
    }
  }
  &_vertical {
    .o-index_item {
      position: relative;
      text-align: center;
      margin-bottom: 30px;
      img{
        border-radius: 3px;
      }
      & > *:last-child:after {
        content: " ";
        width: 50px;
        height: 2px;
        position: absolute;
        bottom: -20px;
        margin-left: -25px;
        left: 50%;
      }
      &.is_orange > *:last-child:after{
        background-color: #e7720f;
      }
      &.is_blue_1 > *:last-child:after{
        background-color: #5b00ff;
      }
      &.is_blue_2 > *:last-child:after{
        background-color: #2adbc3;
      }
      &.is_blue_3 > *:last-child:after{
        background-color: #4990fd;
      }
      &.is_yellow > *:last-child:after{
        background-color: #f5c541;
      }
      &.is_dark-pink > *:last-child:after{
        background-color: #c92d63;
      }
    }
  }
  &_horizontal {
    grid-template-columns: repeat(2, 600px);
    header {
      grid-column-end: 3;
    }
    .o-index_item {
      display: flex;
      flex-direction: row;
      background: #fff;
      position: relative;
      // img{
      //     max-width: 50%;
      // }
      &.is_orange span{
        background-color: #e7720f;
      }
      &.is_blue_1 span{
        background-color: #5b00ff;
      }
      &.is_blue_2 span{
        background-color: #2adbc3;
      }
      &.is_blue_3 span{
        background-color: #4990fd;
      }
      &.is_yellow span{
        background-color: #f5c541;
      }
      &.is_dark-pink span{
        background-color: #c92d63;
      }
      
      span.is_orange {
        background-color: #e7720f;
      }
      span.is_blue_1{
        background-color: #5b00ff;
      }
      span.is_blue_2{
        background-color: #2adbc3;
      }
      span.is_blue_3{
        background-color: #4990fd;
      }
      span.is_yellow{
        background-color: #f5c541;
      }
      span.is_dark-pink{
        background-color: #c92d63;
      }
    }
    .o-index_content {
      padding: 20px 30px;
      display: flex;
      flex-direction: column;
      align-items: baseline;
    }

    .o-more-loader{
      grid-column-start: 1;
      grid-column-end: 3;
      text-align: center;
    }
    p{    
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 4;
      line-height: 1.3;
      max-height: 72px;
      &.date {
        font-size: 16px;
        margin-bottom: 10px;
        margin-top: auto;
      }
    }
    a.o-index_more {
      grid-column-start: 1;
      grid-column-end: 3;
      text-align: center;    
      margin: 90px 0;
    }
    .o-index_button{
      display: flex;
      flex-direction: column;
      span {
        padding: 15px 25px;
        color: #fff;
        border-radius: 3px;
        margin: 3px 0;
      }
    }
    .is-featured{
      img {
        border: 6px solid #00b55b;
      }
      span.featured_text{
        background-color: #00b55b; 
        position:  absolute;
        padding: 18px 20px 15px;
        color:  #fff;
        font-family: "Gotham-Book";
        font-size: rem(14px);
        top: 0px;
        left: 0px;
      }
    } 
  @media only screen and (max-width: 767px) {
  .o-index_more span {
    padding: 15px 45px;
    color: #000;
    border: 2px solid #000;
    border-radius: 3px;
  }
  
  .o-index_item {
    flex-direction: column !important;
  }
  
  a:nth-child(4n + 2) .o-index_item, a:nth-child(4n + 3) .o-index_item {
    flex-direction: column;
  }
  
  p.date {
    position: relative;
    bottom: initial;
    margin: 15px 0;
  }
  
  .o-index_content {
    padding: 25px 15px;
  }
  }
  }
  .o-index_big_tile{
    grid-row-start: 2;
    grid-row-end: 4;
    h3{
      margin: 50px 0;
    }
    & > div {
      flex-direction: column;
    }
    .o-index_content {
        height: 100%;
    }
    .o-index_item {
      flex-direction: column;
      height: 100%;
      img{
        max-width: initial;
      }
    }
  }
  .has_arrow{
    &:after, &:before{
      content: " ";
      position: absolute;
      top: 10px;
      right: 10px;
      @media only screen and (max-width: 767px) {
        top: 15px;
        right: 15px;
      }
    }
    &.is_orange{
      &:after, &:before{
        background-color: #e7720f;
      }
    }
    &.is_blue_1{
      &:after, &:before{
        background-color: #5b00ff;
      }
    }
    &.is_blue_2{
      &:after, &:before{
        background-color: #2adbc3;
      }
    }
    &.is_blue_3{
      &:after, &:before{
        background-color: #4990fd;
      }
    }
    &.is_yellow{
      &:after, &:before{
        background-color: #f5c541;
      }
    }
    &.is_dark-pink{
      &:after, &:before{
        background-color: #c92d63;
      }
    }
    &:before{
      width: 15px;
      height: 40px;
      @media only screen and (max-width: 767px) {
        height: 30px;
        width: 10px;
      }
    }
    &:after{
      width: 40px;
      height: 15px;
      @media only screen and (max-width: 767px) {
        width: 30px;
        height: 10px;
      }
    }
  }
  
  .o-index_item {
    .o-hovered_title{
      display: none;
      bottom: 0px;
      padding: 15px;
      background-color: rgba(0,0,0,0.6);
      color: #fff;
      font-family: "Gotham-Book";
      font-size: rem(16px);
      position: absolute;
    }
    &:hover{
      .o-hovered_title {
        display: block;
      }
    }
  }
 }



.c-index.c-index_mixer{
  a:nth-child(4n + 4) .o-index_item, a:nth-child(4n + 5) .o-index_item {
    flex-direction: row-reverse;
  }
}








@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .c-index.c-index_vertical {
    grid-template-columns: repeat(3, calc(33% - 8px));
    grid-gap: 15px;
  }
  .c-content_container {
    max-width: 100%;
  }
  .o-info-box {
    padding: 10px;
    &:before {
      margin-right: 10px;
    }
    &--large {
      padding: 20px;
      margin: 0px;
      width: calc(100%);
    }
  }
  .main_content_inner {
    padding: 15px;
  }
  .c-index_horizontal {
    grid-template-columns: repeat(2, 50%);
    &.c-index_mixer.gray_back {
      grid-template-columns: repeat(2, calc(50% - 8px));
      grid-gap: 15px;
    }
    .o-index_item {
      flex-direction: column !important;
    }
    p.date {
      position: relative;
      bottom: 0px;
      margin: 15px 0;
    }
  }
}




@media only screen and (min-width: 1025px) and (max-width: 1300px) {

  .c-index.c-index_vertical {
    grid-template-columns: repeat(3, 300px);
  }

  .c-index_horizontal {
    grid-template-columns: repeat(2, 468px);
  }
  .c-index_horizontal .o-index_big_tile p.date {
    position: relative;
    bottom: 0px;
  }
  .c-content_container {
    width: 1025px;
  }

  .c-index .o-index_item .o-hovered_title {
    font-size: 0.75rem;
  }

  .c-index.c-index_horizontal > * {
    margin-bottom: 30px;
  }

  .c-index .o-index_big_tile h3 {
    margin: 20px 0;
  }
}